import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  // strict: true,
  state: {
    url: 'https://challenge.thef2e.com/api/thef2e2019/stage6/rooms',
    data: [],
    fullData: [],
    img: [],
    recently: [],
    booking: [],
  },
  mutations: {
    setData(state, data) {
      state.data = data;
    },
    setFullData(state, data){
      state.fullData = data;
    },
    setImg(state, data) {
      state.img.push(data);
    },
    setRecently(state, id) {
      state.recently = state.recently.filter(item=>{
        return item.id.indexOf(id) === -1
      })
      state.data.items.forEach(item => {
        if (item.id === id) {
          state.recently.push({
            id: item.id,
            url: item.imageUrl,
            name: item.name,
            price: item.normalDayPrice
          })
        }
      })
    },
    setBooking(state, obj){
      state.booking.push(obj);
    }
  },
  actions: {
    DATA_GET: (context) => {
      fetch(context.state.url, {
        headers: {
          'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce'
        }
      }).then(res => {
        return res.json();
      }).then(myJson => {
        context.commit('setData', myJson);
        myJson.items.forEach(item => {
          context.commit('setImg', {
            id: item.id,
            url: item.imageUrl,
            name: item.name
          });
        })
      })
    },
    FULL_DATA_GET: (context, id)=>{
      fetch(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${id}`, {
        headers: {
          'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce'
        }
      }).then(res=>{
        return res.json()
      }).then(myJson=>{
        context.commit('setFullData', myJson);
      })
    },
    BOOKING: (context, obj)=>{
      fetch(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${obj.id}`, {
        method: 'POST',
        headers: {
          'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: obj.name,
          tel: obj.tel,
          date: context.state.booking[context.state.booking.length - 1].date,
        })
      })
      // .then(res=>{
      //   return res.json();
      // }).then(myJson=>{
      //   console.log(myJson)
      // })
    }
  }
})
