<template>
    <div class="content">
        <section class="banner">
            <img src="../assets/Main_img_bg.png" alt="rooms">
        </section>
        <section class="cards">
            <div class="section_title">
                <div class="title_name">
                    <span></span>
                    <h1>OUR BEST ROOMS</h1>
                </div>
                <div class="section_btn">
                    <button @click='changePage("left")'>
                        <i class="material-icons">
                        keyboard_arrow_left
                        </i>
                    </button>
                    <span></span>
                    <button @click='changePage("right")'>
                        <i class="material-icons">
                        keyboard_arrow_right
                        </i>
                    </button>
                </div>
            </div>
            <div class="card_box clearfix">
                <div class="card" v-for="item in roomsImg[index]" :key='item.name'>
                    <div class="card_name">{{ item.name }}</div>
                    <div class="card_img" :style="{backgroundImage: 'url('+ item.url +')'}"></div>
                    <div class="card_content">
                        <p>Single Room is only reserved for one guest. There is a bedroom with a single size bed and a private bathroom...</p>
                        <button @click="roomImformation(item.id)">Show Detail</button>
                    </div>
                </div>
            </div>
        </section>
        <section class="access">
            <div class="section_title">
                <div class="title_name">
                    <span></span>
                    <h1>ACCESS</h1>
                </div>
            </div>
            <div class="access_content clearfix">
                <div class="access_methods">
                    <h2>By Metro & bus</h2>
                    <div>
                        <p>MRT Jiantan Station Exit 3 > Bus 41, Red 30 or "Taipei Children's Amusement Park 2" > Taipei Children's Amusement Park</p>
                        <p>MRT Shilin Station Exit 1 > Bus 255 shuttle, Red 30, 620 or "Taipei Children's Amusement Park 1" > Taipei Children's Amusement Park</p>
                        <p>MRT Zhishan Station Exit 1 > Bus "Taipei Children's Amusement Park 1" > Taipei Children's Amusement Park</p>
                        <p>NOTE: Shuttle bus "Taipei Children's Amusement Park 1" and "Taipei Children's Amusement Park 2" operate during Park opening hours only.</p>
                    </div>
                </div>
                <div class="access_map">
                    <img src="../assets/map2.png" alt="map">
                </div>
            </div>
        </section>
    </div> 
</template>

<script>
export default {
    name: 'HomePage',
    data(){
        return {
            index: 0,
        }
    },
    computed: {
        roomsData(){
            return this.$store.state.data;
        },
        roomsImg(){
            let arr = [[]];
            let imgUrl = this.$store.state.img;
            imgUrl.forEach(item=>{
                if(arr[arr.length-1].length<4){
                    arr[arr.length-1].push({
                        id: item.id,
                        url: item.url,
                        name: item.name
                    })
                }else if(arr[arr.length-1].length===4){
                    arr.push([]);
                    arr[arr.length-1].push({
                        id: item.id,
                        url: item.url,
                        name: item.name
                    });
                }
            })
            return arr;
        }
    },
    methods: {
        changePage(att){
            let length = this.roomsImg.length;
            att === "left" ? this.index -- : this.index ++; 
            this.index = (this.index + length) % length;
        },
        roomImformation(id){
            this.$store.dispatch('FULL_DATA_GET', id);
            this.$router.push({name: 'about', params:{id: id}})
            this.$store.commit('setRecently', id)
        }
    }
}
</script>

<style scoped>
    .clearfix::after{
        content: '';
        display: block;
        clear: both;
    }
    .content {
        width: 100%;
        padding: 0 17.15%;
    }
    .banner {
        margin: 30px 0 50px;
        text-align: center;
    }
    .banner>img {
        width: 62.5vw;
    }
    .cards {
        width: 100%;
    }
    .section_title {
        font-family: 'Open Sans Condensed', sans-serif;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .section_btn>span {
        display: inline-block;
        width: 1px;
        height: 1px;
        background-color: #FFF;
    }
    .section_btn>button {
        width: 26px;
        height: 26px;
        background-color: #1D1B1B;
        color: #FFF;
        border: none;
        outline: none;
        cursor: pointer;
    }
    .title_name {
        height: 41px;
        display: flex;
        align-items: center;
    }
    .title_name>span {
        display: inline-block;
        height: 3px;
        width: 40px;
        background-color: #FFB163;
        margin-right: 15px;
    }
    .title_name>h1 {
        font-size: 1.875rem;
    }
    .card_box {
        width: 100%;
        padding-left: 15px;
        margin-bottom: 50px;
    }
    .card {
        width: 200px;
        height: 430px;
        position: relative;
        font-size: 0.875rem;
        color: #FFF;
        margin: 12px 2.9vw 0 0;
        float: left;
    }
    .card_img{
        width: 200px;
        height: 430px;
        background-size: cover;
        background-position: center;
    }
    .card_name {
        position: absolute;
        top: 10px;
        left: -17px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        background-color: #1D1B1B;
        font-size: 0.875rem;
        padding: 0 5px;
    }
    .card_content {
        position: absolute;
        bottom: 0;
        width: 200px;
        overflow: hidden;
    }
    .card_content::before {
        content: '';
        position: absolute;
        top: 100px;
        left: -1000px;
        bottom: -1000px;
        right: -1000px;
        background-color: rgba(0, 0, 0, 0.7);
        filter: blur(30px);
    }
    .card_content>p {
        position: relative;
        width: 180px;
        margin: auto;
        z-index: 1;
    }
    .card_content>button {
        position: relative;
        width: 180px;
        height: 40px;
        border-radius: 20px;
        border: 2px solid #FFF;
        color: #FFF;
        background-color: transparent;
        outline: none;
        margin: 10px 10px 15px;
        z-index: 1;
        cursor: pointer;
        transition: all .3s;
    }
    .card_content>button:hover {
        background-color: #FFB163;
        border: 2px solid #FFB163;
    }
    .access_content{
        width: 100%;
        margin: 16px 0 61px;
    }
    .access_methods {
        width: 30.55vw;
        float: left;
    }
    .access_methods>div{
        padding-left: 13px;
    }
    .access_methods>h2{
        font-size: 1rem;
        color: #707070;
        margin-bottom: 8px;
    }
    .access_methods p{
        position: relative;
        font-size: 0.875rem;
        color: #858585;
    }
    .access_methods p:not(:nth-last-of-type(1)){
        line-height: 24px;
    }
    .access_methods p:not(:nth-last-of-type(1))::before{
        content: '';
        position: absolute;
        left: -13px;
        top: 7px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        border: 1px solid #858585;
        margin-right: 5px;
    }
    .access_methods p:nth-last-of-type(1){
        font-size: 0.75rem;
        color: #FFB163;
        margin-top: 8px;
    }
    .access_map>img {
        width: 32vw;
        float: right;
    }
</style>